    export default {
        name: 'figure-rule',

	    data() {
            return {
                is_text: false,
            }
	    },
        props: [`theme`, `gift_id`, `turn_no`, `background`, `bless_word`, `nickname`, `avatar`, `type`, `template_message`, `bless_music`, `music_play`],

	    methods: {
			setGift() {
				this.$emit('setGift', false);
			},

			async submit() {
				uni.showLoading({
					title: '获取中'
				});
				if (this.turn_no) {
					const res = await this.$request({
						url: this.$api.gift.get_turn,
						method: 'get',
						data: {
							turn_no: this.turn_no,
						}
					});
					if (res.code === 0) {
						let _this = this;
						uni.navigateTo({
							url: `/plugins/gift/receive/receive?gift_id=${this.gift_id}&status=1`,
							success() {
								_this.setGift();
							}
						})
					} else {
						uni.showModal({
							title: '提示',
							content: res.msg,
						});
						this.setGift();
					}
					uni.hideLoading();
				} else {
					const res = await  this.$request({
						url: this.$api.gift.join,
						method: 'get',
						data: {
							gift_id: this.gift_id
						}
					});
					if (res.code === 0) {
						this.loopAcquisition(res.data);
					} else {
						uni.showModal({
							title: '提示',
							content: res.msg,
						});
						this.setGift();
					}
				}
			},

			async receiveGift() {
				if (this.type !== 'direct_open') {
					this.$subscribe(this.template_message).then(() => {
						this.submit();
					}).catch(() => {
						this.submit();
					});
				} else {
					this.submit();
				}
				
			},

            loopAcquisition(data) {
			    this.$request({
				    url: this.$api.gift.join_status,
				    data: {
				        ...data,
                        gift_id: this.gift_id,
				    },
				    method: 'get',
			    }).then(res => {
                    if (res.code === 0) {
                        if (res.data.hasOwnProperty('list')) {
                            uni.hideLoading();
                            let _this = this;
                            uni.navigateTo({
                                url: `/plugins/gift/receive/receive?gift_id=${this.gift_id}&status=1`,
                                success() {
                                    _this.setGift();
                                }
                            });
                            return res.data;
                        } else {
                            setTimeout(() => {
                                this.loopAcquisition(data);
                            }, 1000);
                        }
                    } else if (res.code === 1) {
                        uni.hideLoading();
                        let status = 2;
                        if (this.type === 'direct_open') {
                            status = 2;
                        } else {
                            status = 3;
                        }
                        uni.navigateTo({
                            url: `/plugins/gift/receive/receive?gift_id=${this.gift_id}&status=${status}`
                        });
                        this.setGift();
                    }
			    });
            },

			play() {
				if (this.music_play) {
					this.$emit('music', false);
				} else {
					this.$emit('music', true, this.bless_music);
				}
			}
		}
    }
